import React, { cloneElement, DOMAttributes, ReactElement } from 'react';
import { IRestProps, mergeRestProps } from '../../common';

export type IIconProps = { size?: 'normal' | 'large' | number, color?: string } & IRestProps & DOMAttributes<SVGElement>;

type IIconInnerProps = { svg20: ReactElement, svg24?: ReactElement };

export const Icon: React.FC<IIconProps & IIconInnerProps> = (props) => {
  const {
    size: _size = 'normal',
    color,
    svg20,
    svg24 = svg20,
    ...rest
  } = props;

  const size = _size === 'normal' ? 20 : _size === 'large' ? 24 : _size;

  return cloneElement(
    size >= 24 ? svg24 : svg20,
    {
      width: size,
      height: size,
      ...mergeRestProps(rest, 'wdn-icon', color ? { color } : undefined)
    }
  );
};

Icon.displayName = 'Icon';










